/* NewsletterSubscription.css */

.outer-container {
    max-width: 1349px; /* Use max-width instead of fixed width */
    width: 100%; /* Allow full width on smaller screens */
    height: auto; /* Allow height to adjust based on content */
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    padding: 20px; /* Add padding for better spacing */
    box-sizing: border-box; /* Include padding in width */
}

.inner-container {
    max-width: 1140px; /* Use max-width instead of fixed width */
    width: 100%; /* Allow full width on smaller screens */
    height: 55px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 8px 0;
}

.header {
    font-family: 'Montserrat', sans-serif;
    font-size: 2.5rem; /* Use rem for scalable font size */
    margin: 0;
    text-align: center; /* Center align for better mobile view */
}

.sub-header-container {
    max-width: 1140px; /* Use max-width instead of fixed width */
    width: 100%; /* Allow full width on smaller screens */
    height: 28px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 12px 0 0;
}

.sub-header {
    font-family: sans-serif;
    font-size: 1rem; /* Use rem for scalable font size */
    margin: 0;
    text-align: center; /* Center align for better mobile view */
}

.email-container {
    max-width: 500px; /* Use max-width instead of fixed width */
    width: 100%; /* Allow full width on smaller screens */
    height: auto; /* Allow height to adjust based on content */
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 10px;
    margin-top: 40px; /* Adjust this value to move the email input down */
}

.email-input {
    width: 100%; /* Full width on small screens */
    height: 100%;
    padding: 8px;
    border: 1px solid #ccc;
    border-radius: 4px;
}

.subscribe-button {
    flex: 1; /* Allow the button to grow and fill available space */
    height: 100%;
    background-color: #11B2A8; /* Button color */
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
}

.subscribe-button:hover {
    background-color: #0056b3; /* Darker blue on hover */
}

/* Responsive Design */
@media (max-width: 768px) {
    .header {
        font-size: 2rem; /* Adjust font size for smaller screens */
    }

    .sub-header {
        font-size: 0.875rem; /* Smaller font size for sub-header */
    }

    .email-container {
        flex-direction: column; /* Stack input and button vertically */
        align-items: stretch; /* Make the input and button full width */
    }

    .subscribe-button {
        width: 100%; /* Full width for button */
        margin-top: 10px; /* Space between input and button */
    }
}

@media (max-width: 480px) {
    .header {
        font-size: 1.5rem; /* Further decrease font size for very small screens */
    }

    .sub-header {
        font-size: 0.75rem; /* Further decrease font size for sub-header */
    }

    .email-input {
        padding: 6px; /* Adjust padding for smaller screens */
    }

    .subscribe-button {
        padding: 10px; /* Adjust padding for button */
    }
}
