.city-section-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  max-width: 1140px;
  padding: 48px;
  box-sizing: border-box;
  margin: 0 auto;
}

.column {
  box-sizing: border-box;
  flex: 1;
  min-width: 300px;
}

.first-column {
  max-width: 570px;
  flex-basis: 45%;
}

.second-column {
  max-width: 570px;
  flex-basis: 45%;
}

.map-image {
  width: 100%;
  height: auto;
  object-fit: cover;
  border-radius: 8px;
}

.additional-info-section {
  background-size: cover;
  border-radius: 20px;
  margin-top: 40px;
  padding: 0 20px;
  margin-left: -100px;
}

.info-container {
  width: 1180px;
  height: 144px;
  margin: 0px;
  display: flex;
  justify-content: space-between;
  box-sizing: border-box;
}

.info-column {
  padding: 40px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.first-info-column {
  width: 500px;
  height: 80px;
  background: #2b2e33 url('https://www.grizzlyforce.ca/help-button-pattern.webp') no-repeat center center;
  border-radius: 16px;
  padding: 20px; /* Added padding for better spacing */
}

.second-info-column {
  width: 500px;
  height: 80px;
  background: #11B2A8 url('https://www.grizzlyforce.ca/help-button-pattern.webp') no-repeat center center;
  border-radius: 16px;
  padding: 20px; /* Added padding for better spacing */
}

.info-heading {
  font-size: 18px;
  font-family: 'Montserrat', sans-serif;
  margin-bottom: 12px;
  color: white;
}

.find-workers,
.find-job {
  font-size: 16px;
  margin: 12px 0 0;
  color: white;
}

.info-content {
  display: flex;
  align-items: center;
}

.arrow-icon {
  width: 12px;
  height: 24px;
  margin-left: 8px;
}

/* Responsive Design */
@media (max-width: 768px) {
  .city-section-container {
    flex-direction: column;
    align-items: center;
  }

  .first-column,
  .second-column {
    max-width: 100%;
    text-align: center;
    margin-bottom: 24px;
  }

  .info-container {
    flex-direction: column;
    width: auto; /* Allow full width on small screens */
    margin: 0; /* Remove margin for smaller screens */
  }

  .info-column {
    max-width: 100%;
    padding: 20px 0;
    text-align: center;
  }
}

@media (max-width: 480px) {
  .info-heading {
    font-size: 16px; /* Adjust heading size for smaller screens */
  }

  .find-workers,
  .find-job {
    font-size: 14px; /* Smaller font size for subheadings */
  }
}
