/* AvettaCertification.css */

.outer-container {
    width: 100%; /* Make the container full width */
    max-width: 1349px; /* Set a maximum width */
    height: auto; /* Adjust height automatically */
    padding: 80px;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    background-image: url("https://www.grizzlyforce.ca/testimonial-bg.webp"); /* Background image */
    background-size: cover; /* Ensure the image covers the container */
    background-position: center; /* Center the background image */
}

.inner-container {
    width: 100%; /* Make inner container full width */
    max-width: 1140px; /* Set a maximum width */
    height: auto; /* Use auto height for flexibility */
    display: flex;
    flex-direction: column;
    align-items: center;
}

.icon-container {
    width: 100%; /* Full width for responsiveness */
    margin: 0 0 8px 0;
    display: flex;
    align-items: center;
    justify-content: center; /* Center the icon horizontally */
}

.avetta-icon {
    width: 40px;
    height: 40px;
}

.certification-header {
    width: 100%; /* Full width */
    height: auto; /* Use auto height */
    display: flex;
    justify-content: center; /* Center the text */
    margin: 12px 0 8px; /* Margin above and below the text */
}

.certification-text {
    font-family: sans-serif;
    font-size: 14px;
    text-align: center; /* Center text */
}

.global-org-container {
    width: 100%; /* Full width */
    height: auto; /* Use auto height */
    display: flex; /* Added this line */
    justify-content: center; /* Center the heading horizontally */
    margin: 0 0 8px 0;
}

.global-org-heading {
    font-family: 'Montserrat', sans-serif;
    font-size: 40px;
    margin: 0;
    text-align: center; /* Center text */
}

.description-container {
    width: 100%; /* Full width */
    max-width: 1108px; /* Set a maximum width */
    height: auto; /* Use auto height */
}

.description-text {
    font-family: sans-serif;
    font-size: 14px;
    margin: 0 0 20px 0;
    text-align: center; /* Center text */
}

.learn-more-container {
    width: 100%; /* Full width */
    max-width: 1140px; /* Set a maximum width */
    height: 60px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.learn-more-link {
    font-family: sans-serif;
    font-size: 16px;
    text-decoration: none; /* Remove underline */
    color: #000; /* Change color as needed */
    display: flex;
    align-items: center;
    transition: color 0.3s; /* Added transition for hover effect */
}

/* Hover effect for the learn more link */
.learn-more-link:hover {
    color: #007BFF; /* Change color on hover (adjust as needed) */
}

/* Optional: Add an icon next to Learn More */
.learn-more-link::after {
    content: "";
    background-image: url("https://www.grizzlyforce.ca/learnMoreArrow.svg");
    background-size: contain;
    width: 20px; /* Adjust size as needed */
    height: 20px; /* Adjust size as needed */
    margin-left: 5px; /* Space between text and icon */
    display: inline-block; /* Ensures the icon displays inline */
}

/* Media Queries for Responsiveness */
@media (max-width: 1200px) {
    .global-org-heading {
        font-size: 36px; /* Slightly smaller heading */
    }
}

@media (max-width: 900px) {
    .outer-container {
        padding: 40px; /* Reduce padding on smaller screens */
    }
    
    .global-org-heading {
        font-size: 32px; /* Smaller heading for mobile */
    }

    .certification-text,
    .description-text {
        font-size: 12px; /* Smaller text size */
    }

    .learn-more-link {
        font-size: 14px; /* Smaller link text */
    }
}

@media (max-width: 600px) {
    .outer-container {
        padding: 20px; /* Further reduce padding */
    }
    
    .global-org-heading {
        font-size: 28px; /* Even smaller heading */
    }

    .certification-text,
    .description-text {
        font-size: 10px; /* Even smaller text size */
    }

    .learn-more-link {
        font-size: 12px; /* Even smaller link text */
    }
}
